<template>
	<div class="bottom-nav">
      <ul class="member-bottomNav">
        <li 
          v-for="(navItem,index) in navConfig"
          :class="{active:curIndex===index}"
          @click="change(navItem,index)" 
        >
        	<a>
        		<i :class="navItem.icon"></i>
        		<div>{{navItem.name}}</div>
        	</a>
        </li>
      <!--   <li>
        	<a href="category.html">
        		<i class="icon-category"></i>
        		<div>分类</div>
        	</a>
        </li>
        <li>
        	<a href="cart.html">
        		<i class="icon-cart"></i>
        		<div>购物车</div>
        	</a>
        </li>
        <li>
          <a href="member.html">
            <i class="icon-user"></i>
            <div>我</div>
          </a>
        </li> -->
      </ul>
    </div>
</template>

<script>
  import qs from 'qs'
  // 通过qs拿到url的参数 一定要截掉问号
  let{index} = qs.parse(location.search.substr(1))

  let navConfig = [
    {
      name: '首页',
      icon: 'icon-home',
      href: 'index.html',
    },
     {
      name: '分类',
      icon: 'icon-category',
      href: 'category.html',
    },
     {
      name: '购物车',
      icon: 'icon-cart',
      href: 'cart.html',
    },
     {
      name: '我',
      icon: 'icon-user',
      href: 'member.html',
    },
  ]
	export default {
		// 开始时少写了个 name属性
		name: 'Bottomnav',
		data(){
			return {
        // 注入导航数据
        navConfig,
        // 把li的下标传递给curIndex 这样动态的active就有了参照
        // 同时做一个容错 万一没有传递过来的值 万一传递的数值有问题
        curIndex: parseInt(index) || 0,
			}
		},//data
		methods:{
      // 通过change方法拿到对应的栏目下标
      change(navItem,index){
        location.href = `${navItem.href}?index=${index}`
      },//change

		},//methods
    created(){
    },//created
	}
</script>

<style type="text/css">

	.bottom-nav {
    position: fixed;
    width: 100%;
    height: 50px;
    bottom: 0;
    background-color: #fff;
    z-index: 8;
    -o-border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
    border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
    border-top: 2px solid #e5e5e5
}

  @media only screen and (-webkit-min-device-pixel-ratio:1.5),
  only screen and (min--moz-device-pixel-ratio:1.5),
  only screen and (min-device-pixel-ratio:1.5) {
    .bottom-nav {
      border-top-width: 1px
    }
  }

  .bottom-nav ul {
    position: relative;
    height: 100%;
    padding-left: 0;
    margin-top: -2px;
    margin-left: -7px;
    zoom: 1
  }

.bottom-nav ul:after {
  content: "";
  display: table;
  clear: both
}

.bottom-nav li {
  display: block;
  float: left;
  width: 25%;
  height: 100%
}

.bottom-nav a {
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 8px;
  text-align: center;
  font-size: 10px
}

.bottom-nav [class^=icon-] {
  display: block;
  width: 22px;
  height: 22px;
  margin: auto;
  margin-bottom: 4px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% auto
}

.bottom-nav .icon-home {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-category {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-cart {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-user {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active a {
  color: #ff6366
}

.bottom-nav .active .icon-home {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-category {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-cart {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-user {
  background-image: url("data:image/png;base64,")
}

	
</style>


